.box{
    background-color: black;
    color: white;
    width: 170px;
    height: 100px;
    font-family: fantasy;
    font-size: large;
    padding: 5px 5px 5px 5px;
    margin: 1px 1px 1px 1px;

}
p{
    padding: 10px 10px 10px 10px;
    margin: 10px 10px 10px 10px;
}
.layout{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
body{
    margin: 0%;
    padding: 0%;
    text-align: center;
    background-color: chartreuse;
}
.size{
    margin-top: 100px;
}
.switch{
    background-color: red;
}